import { useState,useEffect } from "react"
import $ from 'jquery';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faCheck} from '@fortawesome/free-solid-svg-icons'
import styles from './StudyDay.module.css'

function StudyDay({value,callBack}){
    const [day,setDays]=useState(null)
    const [type,setType]=useState(null) //       
    const onSubmit2=(event)=>{
        event.preventDefault()
        const day=Number($('#value2').val())
        if(1>day || day>7){
          alert('1~7    ')
          return 
        }
        setDays(day)
        setType('StudyDay')
      }
      useEffect(()=>{ //value  input 
        $('#value').val('')
      },[value])
      useEffect(()=>{ //   
        callBack(day,type)
      })
    return (
        <div>
          
        <h4 style={{fontWeight:'900',paddingTop:'10px'}}>    : {day}</h4>
        <form onSubmit={onSubmit2}>
            <input placeholder='1~7  ' id="value2" type="text" />
            <button className={styles.button}><FontAwesomeIcon icon={faCheck} style={{color:'green'}}/></button>
        </form>
        </div>
    )
}

export {StudyDay}