import './App.css';
import {useState,useEffect} from 'react'
import 'antd/dist/antd.css';
import { Radio } from 'antd';
import { Select } from './components/Select'
import { StudyHour } from './components/StudyHour';
import { StudyDay } from './components/StudyDay';
import { ResultBtn } from './components/ResultBtn';
import { Fee } from './components/Fee';
import styles from './components/App.module.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faWonSign} from '@fortawesome/free-solid-svg-icons';
function App() {
  const optionsWithDisabled = [
    {
      label: '',
      value: '',
      defaultChecked: true,

    },
    {
      label: '',
      value: '',
    },
  ];
  const periodArr=['','1 (5)','2 (8)','4 (13)']
  const cumArr=['','50 (7)','100 (12)']
  
  
  const [value, setValue] = useState(null);
  const [fee,setFee]=useState(null)
  const [hours,setHours]=useState(null)
  const [days,setDays]=useState(null)
  const [state,setState]=useState(true)
  const stateChange=()=>{
    setState(!state)
  }
  
  
  function callBack(event,type){ //    (app)  
    if(type==='Select') setFee(event)
    else if(type==='StudyHour') setHours(event)
    else if(type==='StudyDay') setDays(event)
  }
  
  return (
    <div className={styles.main} style={{textAlign:'center'}}>
      <div className={styles.fee_button} onClick={stateChange}>
        <FontAwesomeIcon icon={faWonSign}/>
        </div>  
        {state?null:
        <Fee/>}
      

      <h1> </h1>
      
      <div className={styles.background}>
        <h4 style={{fontWeight:'900',textAlign:'center',paddingTop:'10px'}}> </h4>      
        
        <div className={styles.top_container}>
        
            <Radio.Group
              
              onChange={({ target: { value } })=>setValue(value)}
              options={optionsWithDisabled}
              optionType="button"
              buttonStyle="solid"/>
          
            <Select 
            value={value} 
            periodArr={periodArr} 
            cumArr={cumArr}
            callBack={callBack}
            />
        </div>
        
        
        <div className={styles.middle_container}>
          <StudyHour
            value={value}
            callBack={callBack}
            />

          {(value==='')
          ?<StudyDay
            value={value} 
            callBack={callBack}
          />
          :null
          }
        </div>
        <div className={styles.bottom_container}>
          <ResultBtn
            value={value}
            fee={fee}
            hours={hours}
            days={days}
          />
        </div>
      </div>    
      
    </div>
  );
}

export default App;
