import {useEffect, useState} from 'react'
import styles from './ResultBtn.module.css'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faCalculator} from '@fortawesome/free-solid-svg-icons';
const ticketHour=[0,[1],[2],[2,1],[4],[4,1],[6],[6,1],[8],[8,1],[8,2],[8,2,1],[12],[12,1],[12,2],[12,2,1],[12,4],[12,4,1],[12,6],[12,6,1],[12,8],[12,8,1],[12,8,2],[12,8,2,1],[12,12]]
const ticketCost=[0]

for(let i=1;i<ticketHour.length;i++){
    ticketCost[i]=0
    for(let j=0;j<ticketHour[i].length;j++){
        let sum=0
        let total=0
        switch(ticketHour[i][j]){
            case 1:
                sum=2000
               break
            case 2:
                sum=3000
                break
            case 4:
                sum=5000
                break
            case 6:
                sum=7000
                break
            case 8:
                sum=8000
                break
            case 12:
                sum=10000
                break
        }
        ticketCost[i]+=sum
    }
}
function calculate(time,cost,hours){ // , , 
    const first=[]
    const second=[]
    const answer=[]
    if(time%hours===0){
        first.push(ticketHour[hours],ticketCost[hours])
    }
    else{
        first.push(ticketHour[hours],ticketCost[hours],time%hours,ticketCost[time%hours])
    }
    for(let i=hours+1;i<ticketCost.length;i++){
        if(ticketCost[hours]>=ticketCost[i]){
            second.push(ticketCost[i],i) //      
            break;
        }
    }
    answer.push(first,second)
    return answer
}
function ResultBtn({value,fee,hours,days}){
    const [loading,setLoading]=useState(true)
    const [result1,setResult1]=useState(null)
    const [result2,setResult2]=useState(null)
    const [result3,setResult3]=useState(null)
    const [result4,setResult4]=useState(null)
    const [result5,setResult5]=useState(null)
    const change=()=>{
        setResult1(null)
        setResult2(null)
        setResult3(null)
        setResult4(null)
        setResult5(null)
        fee=null
        hours=null
        days=null
    }
    useEffect(()=>{
        change()
    },[value])
    let time=0 //  
    let cost=0 // 
    const resultClick=()=>{

        console.log(value,fee,hours,days)
        
        if(value===null && fee===null && hours===null && days===null){ //  
            alert('   ')
            return
        }
        if(value==='' && (fee===null || hours===null)){ //  
            alert('   ')
            return 
        }
        if(value==='' && (fee===null || hours===null || days===null)){ //  
            alert('   ')
            return
        }
        if(value===''){
            const answer=calculate(time,cost,hours)
            setLoading(false)    
            time=(fee[0]==='5')?50:100
            cost=(time===50)?70000:120000
            setResult1(`  ${fee}     ${hours} .`)
            setResult2(`${parseInt(time/hours)}  `)
            if(time%hours!==0){ //   
                setResult3(` ${parseInt(time%hours)}  `)
                if(answer[0][0].length===1){ //  
                    setResult4(`${parseInt(time/hours)}  ${answer[0][0]} (${answer[0][1]*parseInt(time/hours)} )  + 1  ${ticketHour[time%hours][0]} (${ticketCost[time%hours]}) = ${answer[0][1]*parseInt(time/hours)+ticketCost[time%hours]}`)
                }
                else if(answer[0][0].length===2){
                    setResult4(`${parseInt(time/hours)}  ${answer[0][0]} (${answer[1][0]*parseInt(time/hours)})  + 1  ${ticketHour[time%hours][0]} (${ticketCost[time%hours]}) = ${answer[0][1]*parseInt(time/hours)+ticketCost[time%hours]}`)
                }
            }
            else{ //    
                setResult3(null)
                if(answer[0][0].length===1){
                    setResult4(`${parseInt(time/hours)}  ${answer[0][0]} (${answer[0][1]*parseInt(time/hours)} )  = ${answer[0][1]*parseInt(time/hours)+ticketCost[time%hours]}`)
                }
                else if(answer[0][0].length===2){
                    setResult4(`${parseInt(time/hours)}  ${answer[0][0]} (${answer[1][0]*parseInt(time/hours)})   = ${answer[0][1]*parseInt(time/hours)+ticketCost[time%hours]}`)
                }
            }
            if(answer[1].length===2){ //          
                setResult5(` -> ${parseInt(time/hours)}  ${answer[1][1]} = ${answer[1][0]*parseInt(time/hours)}`)
                // if(time%hours!==0){
                //     setResult5(`${parseInt(time/hours)}  ${answer[1][1]} (${answer[1][0]*parseInt(time/hours)}) + 1  ${ticketHour[time%hours][0]} (${ticketCost[time%hours]}) = ${answer[0][1]*parseInt(time/hours)+ticketCost[time%hours]}`)
                // }  
                // else{
                //     setResult5(`${parseInt(time/hours)}  ${answer[1][1]} = ${answer[1][0]*parseInt(time/hours)}`)
                // }
            }
            else{
                setResult5(null)
            }
            return setLoading(true)
        }
        else if(value===''){
            setResult1(`  ${fee}     ${Number(fee[0])*hours*days} .`)
            setResult2(`${ticketHour[hours]} (${ticketCost[hours]}), ${Number(fee[0])*days}   = ${Number(fee[0])*days*ticketCost[hours]}`)
        }
    }
    return (
        <div>
            <button className={styles.result_button} onClick={resultClick} style={{marginBottom:'10px'}}></button>
            {(value==='')?
                <div>
                    <h4>{result1}</h4>
                    <h4 style={{color:'red'}}>{result2}{result3}</h4>
                    <h4>{result4}</h4>
                    <h4>{result5}</h4>
                </div>
                :
                <div>
                    <h4>{result1}</h4>
                    <h4>{result2}</h4>    
                </div>}
        </div>
    )
}

export {ResultBtn}