import styles from './Fee.module.css'

function Fee(){
    return(
       <div className={styles.background}>
        <div className={styles.container}>
        <div>
        <div className={styles.font}></div>
        <ul>
            <li>1 2,000</li>
            <li>2 3,000</li>
            <li>4 5,000</li>
            <li>6 7,000</li>
            <li>8 8,000</li>
            <li>10 10,000</li>
        </ul>
        </div>
        <div>
        <div className={styles.font}></div>
        <ul>
            <li>1 50,000</li>
            <li>2 80,000</li>
            <li>4 130,000</li>
        </ul>
        </div>
        <div>
        <div className={styles.font}></div>
        <ul>
            <li>50 70,000</li>
            <li>100 120,000</li>
        </ul>
        </div>
        </div>
       </div>
    )
}

export {Fee}