import { useEffect, useState } from "react"
import $ from 'jquery';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faCheck} from '@fortawesome/free-solid-svg-icons'
import styles from './StudyHour.module.css'

function StudyHour({value,callBack}){
    const [hours,setHours]=useState(null)
    const [type,setType]=useState(null) //       
    const onSubmit1=(event)=>{
        event.preventDefault()
        const time=Number($('#value1').val())
        if(1>time || time>12){
          alert('1~12    ')
          return
        }
        setHours(time)
        setType('StudyHour')
      }
      useEffect(()=>{ //value  input 
        $('#value1').val('')
      },[value])
      useEffect(()=>{ //  
        callBack(hours,type)
      }) 
    return (
        <div>
            <h4 style={{fontWeight:'900',paddingTop:'10px'}}>   : {hours}</h4>
            <form onSubmit={onSubmit1}>
                <input placeholder='1~12  ' id="value1" type="text" />
                <button className={styles.button}><FontAwesomeIcon icon={faCheck} style={{color:'green'}}/></button>
            </form>
        </div>
    )
}

export {StudyHour}