import {useEffect, useState} from 'react'
import styles from './Select.module.css'
import $ from 'jquery';

function Select({value,periodArr,cumArr,callBack}){
    const select=document.querySelector('#select')
    const [fee,setFee]=useState(null)
    const [type,setType]=useState(null) //       

    const feeClick=(e)=>{
        e.preventDefault()
        // if(e.target.value===''){
        //     alert('   ')
        //     return
        // }
        setFee(e.target.value)
        setType('Select')
    }

    useEffect(()=>{
        $('#select').val('') //value      
        setFee(null)
      },[value])
    useEffect(()=>{
        callBack(fee,type)
    })
    return (
        <select id='select' defaultValue=''  className={styles.select} onChange={feeClick}>
        {(value==='')
        ?
        periodArr.map((v,i)=>(
          <option  value={v} key={i}>{v}</option>
        ))
        :
        cumArr.map((v,i)=>(
          <option value={v} key={i}>{v}</option>
        ))
        }
      </select>
    )
}

export {Select}